    immediate:true
  },


  methods:{
    load(){
      this.count += 2
    },



    viewdetails(hotelid){
      axios.post('/back/hotel/viewdetails',"hid="+hotelid).then( response => {
        console.log(hotelid)
        if(response.status === 200)
        {
          console.log(response)
          console.log(response.data.result)
          var result2 = '';
          result2 = response.data.result;
          this.$router.push({
            name:'DetailPage',
            query:{
              getresult:result2
            }

          })

        }
      }).catch(failResponse => {}).catch(error => {
        console.log(error.response.data.code)
      })
    },




    myorm(){
      this.$router.push('/ormanage')
    },

    searchresult(){
      this.getresult=this.$route.query.getresult
      let result=this.getresult
      console.log(this.$route.query.getresult)
      console.log(this.getresult)
      for(let i=0; i < result.length ;i++)
      {
        let a={id:i+1,hoteltitle:result[i].hname,hoteladdress:result[i].haddress.addressLine,hotelrating:result[i].starRating,hotelprice:result[i].priceAvg,imageurl:result[i].hphotos[1].url,
        hotelid:result[i].hid}
        var jsonObj = JSON.parse(JSON.stringify(a));
        this.Arr.push(jsonObj)
        console.log(this.Arr)
      }
    }
  }
